.catalog {
  display: flex;
  flex-direction: column;

  &__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  @include onPortable {
    align-items: flex-start;
    justify-content: flex-start;
  }

  &__count {
    color: $secondary-color;
    margin-bottom: 32px;
  }

  &__filters {
    display: flex;
    gap: 16px;
    margin-bottom: 16px;
  }

  &__item-list {
    display: grid;
    flex-direction: column;
    grid-template-columns: repeat(1, 1fr);
    gap: 16px;
    margin: 0 auto 40px;

    @include onPortable {
      display: grid;

      grid-template-columns: repeat(2, 1fr);
    }

    @include onDesktop {
      grid-template-columns: repeat(4, 1fr);
    }
  }
}
